html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
margin: 0;
padding: 0;
border: 0;
font-size: 100%;
vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
display: block;
}
ol, ul {
list-style: none;
}
blockquote, q {
quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
content: '';
content: none;
}
table {
border-collapse: collapse;
border-spacing: 0;
}

html {
  height: 100%;
  width: 100%
}

body {
	margin: 0 auto;
  	height: 100vh;
  	width: 100%;

  	/* IE10+ */

	/* Mozilla Firefox */

	/* Opera */ 
	background-image: -o-linear-gradient(top, #320546 0%, #9E9511 100%);

	/* Webkit (Safari/Chrome 10) */ 
	background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #320546), color-stop(100, #9E9511));

	/* Webkit (Chrome 11+) */ 
	background-image: -webkit-linear-gradient(top, #320546 0%, #9E9511 100%);

	/* W3C Markup */ 
	background-image: -webkit-gradient(linear, left top, left bottom, from(#320546), to(#9E9511)); 
	background-image: linear-gradient(to bottom, #320546 0%, #9E9511 100%);
  }

